li {
  list-style-type: none;
}

a {
  text-decoration: none;
}

.tui-pagination * {
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  color: #fff;
}

.tui-ico-ellipsis,
.tui-ico-first,
.tui-ico-last,
.tui-ico-next,
.tui-ico-prev {
  overflow: hidden;
}

.tui-pagination {
  height: 28px;
  margin: 60px 0;
  line-height: normal;
  text-align: center;
  font-size: 0;

  @media screen and (min-width: 1280px) {
    margin: 80px 0;
  }
}

.tui-pagination .tui-page-btn {
  display: inline-block;
  position: relative;
  width: 30px;
  padding: 8px 0 6px;
  margin-left: 5px;
  color: #fff;
  font-size: 12px;
  font-weight: normal;
  line-height: 1;
  text-decoration: none;
  vertical-align: middle;
  border: 1px solid inherit;
  border-width: 1px 0;
  border-radius: 10px 0px;

  &:hover {
    box-shadow: 1px 1px 10px 5px #dc56c5;
  }
}

.tui-pagination .tui-page-btn:hover {
  color: #fff;
  transform: scale(1.05);
}

.tui-pagination .tui-is-selected,
.tui-pagination strong {
  color: #fff;
  border: 1px solid #dc56c5;
  border-radius: 10px 0px;
  cursor: default;
}

/* .tui-pagination .tui-is-selected:hover {
  background-color: #dc56c5;
} */

.tui-pagination .tui-first-child.tui-is-selected,
.tui-pagination strong.tui-first-child {
  margin-left: 0;
}

.tui-pagination .tui-first-child,
.tui-pagination .tui-prev-is-ellip {
  border-left: 1px solid inherit;
}

.tui-pagination .tui-last-child,
.tui-pagination .tui-next-is-ellip {
  border-right: 1px solid inherit;
}

.tui-pagination .tui-first-child.tui-is-selected {
  border-left: 1px solid #dc56c5;
}

.tui-pagination .tui-last-child.tui-is-selected {
  border-right: 1px solid #dc56c5;
}

.tui-pagination .tui-first,
.tui-pagination .tui-prev,
.tui-pagination .tui-next,
.tui-pagination .tui-last,
.tui-pagination .tui-prev-is-ellip,
.tui-pagination .tui-next-is-ellip {
  overflow: hidden;
  height: 26px;
  padding: 0;
  font-size: 0;
  line-height: 26px;
  white-space: nowrap;
  font-weight: normal;
  border: 1px solid inherit;
}

.tui-pagination .tui-prev-is-ellip {
  border-right: 0;
}

.tui-pagination .tui-next-is-ellip {
  border-left: 0;
}

.tui-pagination .tui-first+.tui-prev,
.tui-pagination .tui-next+.tui-last {
  border-left: 0;
}

.tui-pagination .tui-ico-first,
.tui-pagination .tui-ico-prev,
.tui-pagination .tui-ico-next,
.tui-pagination .tui-ico-last,
.tui-pagination .tui-ico-ellip {
  background: url('../images/sp_btn.png') no-repeat 0 0;
  display: inline-block;
  vertical-align: middle;
  font-size: 0px;
}

.tui-pagination .tui-prev-is-ellip,
.tui-pagination .tui-next-is-ellip {
  /* display: none; */
  fill: #fff;
  padding: 0;
  line-height: 1;
}

.tui-pagination .tui-prev {
  margin-right: 10px;
}

.tui-pagination .tui-next {
  margin-left: 10px;
  fill: #fff;
}

.tui-pagination .tui-ico-first {
  width: 11px;
  height: 10px;
  background-position: -13px 0px;
}

.tui-pagination .tui-ico-prev {
  width: 6px;
  height: 10px;
  background-position: -26px -12px;
}

.tui-pagination .tui-ico-next {
  width: 6px;
  height: 10px;
  background-position: -21px -24px;
}

.tui-pagination .tui-ico-last {
  width: 11px;
  height: 10px;
  background-position: -13px -12px;
}

.tui-pagination .tui-ico-ellip {
  width: 10px;
  height: 2px;
  margin-top: 16px;
  vertical-align: top;
  background-position: 0px -24px;
}

.tui-pagination .tui-is-disabled:hover {
  background: inherit;
  box-shadow: none;
  cursor: default;
  transform: scale(1.05);
}

.tui-pagination .tui-is-disabled .tui-ico-prev {
  background-position: -26px 0px;
}

.tui-pagination .tui-is-disabled .tui-ico-first {
  background-position: 0px 0px;
}

.tui-pagination .tui-is-disabled .tui-ico-next {
  background-position: -13px -24px;
}

.tui-pagination .tui-is-disabled .tui-ico-last {
  background-position: 0px -12px;
}

.tui-page-inputs {
  display: inline-block;
  vertical-align: top;
}

.tui-page-inputs .tui-page-input {
  display: inline-block;
  width: 10px;
  height: 12px;
  padding: 8px 0 6px;
  font-size: 12px;
  text-align: center;
  color: #fff;
  border: 0;
  outline: none;
}

.tui-page-inputs .tui-page-current {
  color: #dc56c5;
}

.tui-page-separator {
  display: inline-block;
  font-size: 12px;
  height: 12px;
  padding: 8px 0 6px;
  vertical-align: top;
}